<template>
  <div class="user-page">
    <div class="user">
      <img :src="avatar" alt="" />
      <h3>{{username}}</h3>
    </div>
    <van-grid clickable :column-num="3" :border="false" class="mb20">
      <van-grid-item icon="clock-o" text="历史记录" to="/" />
      <van-grid-item icon="bookmark-o" text="我的收藏" to="/collect" />
      <van-grid-item icon="thumb-circle-o" text="我的点赞" to="/like" />
    </van-grid>

    <van-cell-group class="mt20">
      <van-cell title="推荐分享" is-link />
      <van-cell title="意见反馈" is-link />
      <van-cell title="关于我们" is-link />
      <van-cell  title="退出登录" is-link @click="logout()" />
      <van-cell  title="切换账号" is-link @click="showConfirmDialog()" />
    </van-cell-group>
  </div>
</template>

<script>
import { removeToken } from '@/utils/storage'
import { getUserInfo } from '@/api/user'
import { Dialog } from 'vant'

export default {
  name: 'user-page',
  data () {
    return {
      username: '',
      avatar: ''
    }
  },
  async created () {
    const { data } = await getUserInfo()
    this.username = data.username
    this.avatar = data.avatar
  },
  methods: {
    logout () {
      if (window.confirm('确定要退出吗')) {
        removeToken()
        this.$router.push('/login')
      } else {
        //
      }
    },
    showConfirmDialog () {
      Dialog.confirm({
        title: '切换账号',
        message: '确定要退出账号吗'
      }).then(() => {
      // 用户点击确认按钮后的回调
        removeToken()
        this.$router.push('/login')
      }).catch(() => {
      // 用户点击取消按钮后的回调
      })
    }

  }

}
</script>

<style lang="less" scoped>
.user-page {
  padding: 0 10px;
  background: #f5f5f5;
  height: 100vh;
  .mt20 {
    margin-top: 20px ;
  }
  .mb20{
    margin-bottom: 20px;
  }
  .user {
    display: flex;
    padding: 20px 0;
    align-items: center;
    img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
    }
    h3 {
      margin: 0;
      padding-left: 20px;
      font-size: 18px;
    }
  }
}
</style>
