// 封装和用户相关的请求函数
// 1.导入 request.js

import instance from '@/utils/request'

// 封装注册的接口函数 按需导出
export const register = data => {
  return instance.post('/h5/user/register', data)
}
export const login = data => {
  return instance.post('/h5/user/login', data)
}

// 获取人员信息
export const getUserInfo = () => {
  return instance('/h5/user/currentUser')
}
